* {
  box-sizing: inherit;
}

body {
  margin: 0;
}

p {
  margin: 0 0 14.4px;
}

pre.highlight {
  overflow-x: auto;
  padding: 10px;
}

.nav {
  background-color: #333;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 2rem;
  padding: 0 1rem;
}

.site-title {
  font-size: 2rem;
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 1rem;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: .25rem;
}

.nav li.active {
  background-color: #555;
}

.nav li:hover {
  background-color: #777;
}

.post-header-container {
  width: 100%;
  text-align: center;
}

.scrim {
  padding: 2em 1em 1em 1em;
}

.post-title {
  font-size: 2em;
}

.wrapper {
  max-width: 48em;
  margin-left: auto;
  margin-right: auto;
  padding: 1em;
}

.page-divider {
  display: block;
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  border-top: 1px solid #ddd;
}

.page-divider .one {
  position: relative;
  top: -9px;
  display: block;
  background: #ddd;
  width: 18px;
  height: 18px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 50%;
}

.page-divider .two {
  position: relative;
  top: -26px;
  display: block;
  background: #fff;
  width: 16px;
  height: 16px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 50%;
}

.page-content .post-meta {
  max-width: 48em;
  margin-left: auto;
  margin-right: auto;
  color: #999;
}

.page-content .post .post-meta {
  padding-bottom: 1em;
}

.page-content .post-meta .post-date{
  float: left;
  display: block;
  margin-right: 2.35%;
  width: 48.82%;
  text-align: left;
  font-size: 0.9em;
}

.page-content .post-meta .post-categories:last-child {
  margin-right: 0;
}

.page-content .post-meta .post-categories {
  float: left;
  display: block;
  /*margin-right: 2.35%;*/
  width: 48.82%;
  text-align: right;
  font-size: 0.9em;
}

.page-content .post .post-content {
  padding: 1em 0;
}

code.highlighter-rouge {
  color: #EB5757;
  background: rgba(135, 131, 120, 0.15);
  border-radius: 3px;
  font-size: 85%;
  padding: 0.2em 0.4em;
}

.highlight {
  color: #ffffff;
  background-color: #272822;
}